.pizzalist {
  display: flex;
  flex-direction: row;
  width: 95%;
  height: fit-content;
  box-shadow: inset 0 0 8px 4px rgba(0, 0, 0, 0.45);
  border-radius: 5vw;
  margin-left: 2.5%;
  justify-content: left;
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  align-items: center;
}

.pizzatext {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: left;
  margin-left: 2rem;
}
.pizzatext h2 {
  text-transform: uppercase;
  font-size: 2vw;
}
.pizzatext h3 {
  font-weight: lighter;
  font-size: 1vw;
}

.pizzalist img {
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  margin-left: 1rem;
  box-shadow: 2px 0px 6px 8px rgba(0, 0, 0, 0.45);
}
@keyframes imgHover {
  from {
    border-radius: 50%;
  }
  to {
    border-radius: 25%;
    transform: scale(1.25);
  }
}
.pizzalist img:hover {
  animation-name: imgHover;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
}

.pizzalist .btn {
  margin-left: 6%;
  margin-right: 2%;
  width: 15%;
  display: flex;
  flex-direction: row;
  justify-content: left;
  box-shadow: inset 0px 0px 8px 4px rgba(0, 0, 0, 0.45);
  padding: 1rem 1.5rem 1rem 1.5rem;
  font-size: 1.5vw;
  border-radius: 5vw;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.pizzalist .btn i {
  margin-left: 1vw;
  margin-top: 1%;
  cursor: pointer;
}

.pizzalist .btn i:hover {
  color: rgb(200, 16, 43);
}

@media only screen and (max-width: 900px) {
  .pizzalist img {
    width: 15vw;
    height: 15vw;
  }
  .pizzatext h2 {
    font-size: 3vw;
  }
  .pizzatext h3 {
    font-size: 2vw;
  }
  .pizzalist .btn {
    font-size: 2.5vw;
    padding: 1rem 0.5rem 1rem 0.5rem;
    width: 25%;
  }
}
